.flex{
	margin: 6px 0px auto;

}
 #respTable {
	width: 91%;
	display: table;
}

#respTableCaption{
		display: table;
		margin: 0 auto;
		margin-top: 10px;
		text-align: start;
		font-size: 30px;
		font-weight:normal;
}

#respTableBody{
	display: table-row-group;
}

.respTableRow{
	display: table-row;
}

.tableBodyCell{
	display: table-cell;  
	/* padding: 5px; */
	font-size: medium;
}

